:root {
    --foreground-color: #FAF3ED;
    --background-color: #120B03;
    --accent-color1: #3B2409;
    --accent-color2: #8B6237;
    --accent-color3: #D9B381;
}

.app__navbar {
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    background: rgba(18, 11, 3, 0.6);
    position: fixed;
    z-index: 2;

    img {
        width: 80px;
        height: 80px;
        padding: 10px;
    }
}

.app__navbar-logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;

}

.app__navbar-links {
    padding: 2rem 8rem;
    flex: 1;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    list-style: none;
    font-family: sans;


    li {
        margin: 0 1rem;
        cursor: pointer;

        flex-direction: column;

        div {
            width: 5px;
            height: 5px;
            background: transparent;
            border-radius: 50%;

            margin-top: 5px;
        }

        a {
            color: var(--foreground-color);
            text-decoration: none;
            flex-direction: column;

            text-transform: uppercase;
            font-weight: 700;
            font-size: 2rem;

            transition: all 0.3s ease-in-out;

            &:hover {
                color: var(--accent-color3);
            }
        }

    }

    @media screen and (max-width:900px) {
        display: none;
    }
}

.app__navbar-menu {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    position: relative;

    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--secondary-color);

    svg {
        width: 70%;
        height: 70%;
        color: var(--white-color);
    }

    div {
        position: fixed;
        top: 0;
        bottom: 0;
        right: 0;
        z-index: 5;

        padding: 1rem;

        width: 80%;
        height: 100vh;

        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        flex-direction: column;

        // background: url('../../assets/bgWhite.png');
        background-color: var(--white-color);
        background-size: cover;
        background-repeat: repeat;

        box-shadow: 0px 0px 20px rgba(168, 168, 168, 0.15);

        svg {
            width: 35px;
            height: 35px;
            color: var(--secondary-color);
            margin: 0.5rem 1rem;
        }

        ul {
            list-style: none;
            margin: 0;
            padding: 0;
            height: 100%;
            width: 100%;

            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column;

            li {
                margin: 1rem;

                a {
                    color: var(--gray-color);
                    text-decoration: none;
                    font-size: 1rem;
                    text-transform: uppercase;
                    font-weight: 500;

                    transition: all 0.3s ease-in-out;
                }

                &:hover {
                    color: var(--secondary-color);
                }
            }
        }

        @media screen and (min-width:900px) {
            display: none;
        }
    }

    @media screen and (min-width:900px) {
        display: none;
    }
}
